import React from 'react';
import 'bulma/css/bulma.css';
import './App.scss';
import classNames from 'classnames';

export const goods = [
  'Dumplings',
  'Carrot',
  'Eggs',
  'Ice cream',
  'Apple',
  'Bread',
  'Fish',
  'Honey',
  'Jam',
  'Garlic',
];

type State = {
  selectedGood: string;
};

export class App extends React.Component<{}, State> {
  state = {
    selectedGood: 'Jam',
  };

  handleGoodButtonClick = (condition: boolean, nameToSet: string) => {
    if (condition) {
      this.setState({
        selectedGood: '',
      });
    } else {
      this.setState({
        selectedGood: nameToSet,
      });
    }
  };

  handleClearButtonClick = () => {
    this.setState({
      selectedGood: '',
    });
  };

  render() {
    const { selectedGood } = this.state;

    return (
      <main className="section container">
        {!selectedGood
          ? (
            <h1 className="title">
              No goods selected
            </h1>
          )
          : (
            <h1 className="title is-flex is-align-items-center">
              {`${selectedGood} is selected`}

              {/* eslint-disable-next-line jsx-a11y/control-has-associated-label */}
              <button
                data-cy="ClearButton"
                type="button"
                className="delete ml-3"
                onClick={this.handleClearButtonClick}
              />
            </h1>
          )}

        <table className="table">
          <tbody>
            {goods.map(good => {
              const isGoodSelected = selectedGood === good;

              return (
                <tr
                  key={good}
                  data-cy="Good"
                  className={classNames(
                    {
                      'has-background-success-light': isGoodSelected,
                    },
                  )}
                >
                  <td>
                    <button
                      data-cy={isGoodSelected
                        ? 'RemoveButton'
                        : 'AddButton'}
                      type="button"
                      className={classNames(
                        'button',
                        {
                          'is-info': isGoodSelected,
                        },
                      )}
                      onClick={() => {
                        this.handleGoodButtonClick(isGoodSelected, good);
                      }}
                    >
                      {isGoodSelected
                        ? '-'
                        : '+'}
                    </button>
                  </td>

                  <td data-cy="GoodTitle" className="is-vcentered">
                    {good}
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </main>
    );
  }
}
